    -webkit-transition-duration: 0s linear;
    transition-duration: 0s linear;
}
.wh_indicator {
    position: absolute;
    bottom: 8px;
    width: 100%;
    text-align: center;
    background: 0 0;
}
.wh_indicator_item {
    display: inline-block;
